.add-music-container-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}
.add-music-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.search-and-music-container {
  background-color: white;
  display: flex;
  height: 70%;
  width: 40%;
}

.music-search-result {
  border-left: 2px solid;
  overflow: scroll;

}

.music-search-item {
  border-bottom: 2px solid;
  background-color: white;;
}
.music-search-item:hover {
    background-color: #ffc9d7;
  }